import { Component, Host, h, Prop, EventEmitter, Event } from '@stencil/core';
import { FleButtonAppearance, FleButtonSize, FleButtonStyle } from './fle-button.model';

@Component({
  tag: 'fle-button',
  styleUrl: 'fle-button.scss',
  shadow: true
})
export class FleButton {
  // @Prop() title: string;
  @Prop() styleType: FleButtonStyle = 'primary';
  @Prop() appearance: FleButtonAppearance = 'solid';
  @Prop() size: FleButtonSize = 'medium';

  @Event({
    eventName: 'buttonClicked',
  }) buttonClicked: EventEmitter<void>;

  handleClick() {
    this.buttonClicked.emit();
  }

  render() {
    const classListFromProps = this.generateClassList();
    return (
      <Host>
        <button class={classListFromProps} onClick={() => this.handleClick()}>
          <slot>
          </slot>
        </button>
      </Host>
    );
  }

  private generateClassList(): string {
    const styleClass = this.styleType === 'primary' ? 'primary-style' : 'secondary-style';
    const appearanceClass = this.appearance === 'solid' ? 'solid-appearance' : 'outline-appearance';
    const sizeClass = this.size === 'small' ? 'small-size' : 'medium-size';

    return `${styleClass} ${appearanceClass} ${sizeClass}`;
  }
}
